<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom:20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入用户ID、登录名"
                      v-model="currencyTrans.username">
            </el-input>
            <el-button class="filter-item" @click="conditionQuery" type="primary" style="margin-left: 30px">查询
            </el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间">
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="currencyTrans.page.pageNum"
                               :page-sizes="[10,20,30, 50]" :page-size="currencyTrans.page.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import DCTP from '@/utils/dctp';
    import {rmbquery} from '@/api/awaits';
    //启用禁用筛选条件
    const allCoin = {baseSeq: 0, chineseName: '全部类型', englishName: ""};
    const statuScope = [
        {key: 0, display_name: '全部状态'},
        {key: 2, display_name: '等待审核'},
        {key: 3, display_name: '成功提现'},
        {key: 4, display_name: '系统撤销'},
        {key: 5, display_name: '用户撤销'},
        {key: 6, display_name: '正在处理'},
    ];
    export default {
        data() {
            return {
                //搜索需求数据等
                allCoin,
                statuScope,
                coinList: [],
                dateTime: '',
                tableData: [],
                total: null,
                lockWithdrawalVo: {
                    id: '',
                    status: ''
                },
                currencyTrans: {
                    transStatus: 3,
                    type: 2,
                    username: "",
                    page: {
                        pageNum: 1,
                        pageSize: 10
                    }
                },
                msg: '',
                listLoading: true,
                tableHeader: [
                    {
                        label: "单号id",
                        prop: "id",
                        width: 90
                    },
                    {
                        label: "会员UID",
                        prop: "userId",
                        width: 100
                    }, {
                        label: "登录名",
                        prop: "phoneNum",
                        width: 100
                    }, {
                        label: "会员昵称",
                        prop: "alaisName",
                        width: 125
                    }, {
                        label: "真实姓名",
                        prop: "username",
                        width: 110
                    }, {
                        label: "状态",
                        prop: "transStatus",
                        width: 125,
                        align: 'center'
                    }, {
                        label: "提现金额",
                        prop: "actualmoney",
                        width: 95,
                        align: 'center'
                    }, {
                        label: "手续费",
                        prop: "chargefee",
                        width: 110
                    },{
                        label: "银行",
                        prop: "payBankName",
                        width: 120,
                        align: 'center'
                    }, {
                        label: "提现账号",
                        prop: "account",
                        width: 220,
                        align: 'center',
                        formatter(row, column, value) {
                            if (row.accountStatus !== 3) {
                                return "********";
                            }
                            return row.account;
                        }
                    },  {
                        label: "创建时间",
                        prop: "createTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }, {
                        label: "最后修改时间",
                        prop: "updateTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }]
            }
        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {


            //获取当前页数
            userPage(val) {
                this.currencyTrans.page.pageNum = val;
                this.getList();
            }
            ,
            //获取每页多少条
            userPageSize(val) {
                this.currencyTrans.page.pageSize = val;
                this.getList();
            }

            ,
            //条件查询方法
            conditionQuery() {
                this.getList();
            },
            /**
             * 请求列表方法
             */
            getList() {
                this.listLoading = true;
                rmbquery(this.currencyTrans).then(response => {
                    this.tableData = response.data.data;
                    this.total = response.data.page.count;
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                    this.listLoading = false;
                })
            }
        }
        ,
        created() {
            this.getList();
        }
    }
</script>



